* {
  margin: 0;
  padding: 0;
  font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
}
::-webkit-scrollbar {
  height: 16px;
  width: 16px;
  overflow: visible;
  background: #ffffff;
}
::-webkit-scrollbar-track {
  background-clip: padding-box;
  border: solid transparent;
  border-width: 0 0 0 4px;
  box-shadow: none;
  margin: 0 4px;
}
::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  background-clip: padding-box;
  min-height: 28px;
  padding: 100px 0 0;
  border-style: solid;
  border-color: transparent;
  border-width: 4px;
  background-color: #dadce0;
  border-radius: 8px;
  box-shadow: none;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
::-webkit-scrollbar-button {
  height: 0;
  width: 0;
}
.x-sheet {
  width: 100%;
  height: 100%;
  font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  user-select: none;
  contain: content;
}
.x-sheet-layer-bar {
  width: 100%;
  height: 100%;
}
.x-sheet-vertical-layer {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}
.x-sheet-vertical-layer-element {
  flex-grow: 0;
  overflow: hidden;
}
.x-sheet-horizontal-layer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.x-sheet-horizontal-layer-element {
  flex-grow: 0;
  flex-basis: 0;
  overflow: hidden;
}
.x-sheet-horizontal-center {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.x-sheet-horizontal-center-element {
  height: 100%;
}
.x-sheet-vertical-center {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.x-sheet-vertical-center-element {
  width: 100%;
}
.x-sheet-scroll-bar-x {
  width: 100%;
  height: 17px;
  background: #dfdfdf;
  position: relative;
}
.x-sheet-scroll-bar-x-last-but {
  width: 15px;
  height: 100%;
  float: left;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-x-next-but {
  width: 15px;
  height: 100%;
  float: right;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-x-content {
  position: absolute;
  left: 15px;
  right: 15px;
  top: 2px;
  bottom: 2px;
}
.x-sheet-scroll-bar-x-block {
  position: absolute;
  width: 100px;
  height: 100%;
  box-sizing: border-box;
  left: 0;
  background: #ffffff;
  border-radius: 6px;
  border: 1px solid #bbb;
}
.x-sheet-scroll-bar-y {
  width: 17px;
  height: 100%;
  background: #dfdfdf;
  position: relative;
}
.x-sheet-scroll-bar-y-last-but {
  width: 100%;
  height: 15px;
  position: absolute;
  top: 0;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-y-next-but {
  width: 100%;
  height: 15px;
  position: absolute;
  bottom: 0;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-y-content {
  position: absolute;
  top: 15px;
  left: 2px;
  right: 2px;
  bottom: 15px;
  z-index: 2;
}
.x-sheet-scroll-bar-y-block {
  position: absolute;
  width: 100%;
  height: 100px;
  box-sizing: border-box;
  top: 0;
  background: #ffffff;
  border-radius: 6px;
  border: 1px solid #bbb;
}
.x-sheet-el-pop-up {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 6px 2px rgba(60, 64, 67, 0.15);
}
.x-sheet-drag-panel {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
}
.x-sheet-drag-panel-mask {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
.x-sheet-el-context-menu {
  padding: 10px;
}
.x-sheet-el-context-menu-item {
  cursor: pointer;
}
.x-sheet-el-context-menu-item.hover:hover {
  background: #efefef;
}
.x-sheet-el-context-menu-item-divider {
  display: inline-block;
  border-bottom: 1px solid #dbdbdb;
  width: 100%;
  vertical-align: middle;
  height: 0;
}
.x-sheet-color-array {
  overflow: hidden;
  width: 216px;
}
.x-sheet-color-array-item {
  width: 20px;
  height: 20px;
  float: left;
  margin: 1px;
  border-radius: 100%;
  border: 1px solid #e7e7e7;
  display: flex;
  justify-content: center;
  align-items: center;
}
.x-sheet-color-picker {
  background: #ffffff;
  cursor: default;
  user-select: none;
  padding: 0 5px 0 5px;
}
.x-sheet-color-picker-pre-view-color-point {
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background: black;
}
.x-sheet-color-picker-color-value-tips {
  font-size: 14px;
  color: #787878;
}
.x-sheet-color-picker-color-input {
  border: 1px solid #dadce0;
  width: 90px;
  height: 36px;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 0 3px 0 3px;
  font-size: 15px;
}
.x-sheet-color-picker-top {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 45px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.x-sheet-color-picker-select-color-point {
  position: absolute;
  top: -10px;
  left: -10px;
  width: 20px;
  height: 20px;
  background: #ff8000;
  border: 2px solid white;
  border-radius: 20px;
  box-shadow: 0 0 3px 1px #bdc1c6;
  cursor: pointer;
}
.x-sheet-color-picker-select-color-area {
  background: #42a642;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
.x-sheet-color-picker-select-color-area.area2 {
  background: linear-gradient(to right, #ffffff, rgba(255, 255, 255, 0));
}
.x-sheet-color-picker-select-color-area.area3 {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
}
.x-sheet-color-picker-center {
  position: relative;
  height: 246px;
  width: 246px;
  margin: 10px;
}
.x-sheet-color-picker-color-hux-tips {
  color: #787878;
  font-size: 13px;
  margin-bottom: 5px;
}
.x-sheet-color-picker-color-bar {
  width: 100%;
  height: 15px;
  border-radius: 15px;
  position: relative;
  background: linear-gradient(to right, #ff0000, #ff0080, #ff00ff, #8000ff, #0000ff, #0080ff, #00ffff, #00ff80, #00ff00, #80ff00, #ffff00, #ff8000, #ff0000);
}
.x-sheet-color-picker-bar-point {
  position: absolute;
  top: -5px;
  left: -10px;
  width: 20px;
  height: 20px;
  background: #ff8000;
  border: 2px solid white;
  border-radius: 20px;
  box-shadow: 0 0 3px 1px #bdc1c6;
  cursor: pointer;
}
.x-sheet-color-picker-bottom {
  margin: 10px;
}
.x-sheet-color-picker-button {
  height: 35px;
  width: 90px;
  float: right;
  cursor: pointer;
  line-height: 35px;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  border: 1px solid #dadce0 !important;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  border-radius: 4px;
}
.x-sheet-color-picker-button.cancel {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 5px;
  background: white;
  color: #188038;
}
.x-sheet-color-picker-button.select {
  margin-right: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
  background: #188038;
  color: #ffffff;
}
.x-sheet-color-picker-buttons {
  width: 100%;
  overflow: hidden;
  margin-bottom: 10px;
}
.x-sheet-alert {
  width: 512px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 8px;
  box-shadow: 0 4px 8px 3px rgba(60, 64, 67, 0.15);
  padding: 24px;
  cursor: default;
}
.x-sheet-alert-title {
  color: #202124;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 28px;
  margin-bottom: 15px;
}
.x-sheet-alert-buttons {
  overflow: hidden;
  margin-top: 10px;
}
.x-sheet-alert-ok {
  border: 1px solid transparent!important;
  border-radius: 4px;
  box-sizing: border-box;
  float: right;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 14px;
  height: 36px;
  letter-spacing: 0.25px;
  line-height: 16px;
  padding: 9px 0 11px 0;
  color: #fff;
  background: #2a8947;
  box-shadow: 0 1px 3px 1px rgba(52, 168, 83, 0.15);
  text-align: center;
  width: 72px;
  cursor: pointer;
}
.x-sheet-confirm {
  width: 512px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 8px;
  box-shadow: 0 4px 8px 3px rgba(60, 64, 67, 0.15);
  padding: 24px;
  cursor: default;
}
.x-sheet-confirm-title {
  color: #202124;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 28px;
  margin-bottom: 15px;
}
.x-sheet-confirm-buttons {
  overflow: hidden;
  margin-top: 10px;
}
.x-sheet-confirm-button {
  border: 1px solid transparent!important;
  border-radius: 4px;
  box-sizing: border-box;
  float: right;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 14px;
  height: 36px;
  letter-spacing: 0.25px;
  line-height: 16px;
  padding: 9px 0 11px 0;
  color: #fff;
  background: #2a8947;
  box-shadow: 0 1px 3px 1px rgba(52, 168, 83, 0.15);
  text-align: center;
  width: 72px;
  cursor: pointer;
}
.x-sheet-confirm-ok {
  margin-right: 10px;
}
.x-sheet-confirm-no {
  background: #f8fcf9;
  border: 1px solid #c8e7d1 !important;
  color: #188038;
}
.x-sheet-form-search-input {
  height: 100%;
  overflow: hidden;
}
.x-sheet-form-search-input .x-sheet-form-input-wrap {
  height: 100%;
  width: 90%;
  float: left;
}
.x-sheet-form-search-input .x-sheet-form-input-inner {
  padding: 0 5px 0 5px;
  height: 100%;
}
.x-sheet-form-search-input .x-sheet-form-input-source {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  color: #414141;
  display: inline-block;
}
.x-sheet-form-search-input .x-sheet-form-input-search {
  height: 100%;
  width: 10%;
  float: left;
  cursor: pointer;
  background-image: url(data:image/svg+xml;base64,);
  background-position: center center;
  background-size: 60% 60%;
  background-repeat: no-repeat;
}
.x-sheet-form-plain-input {
  height: 100%;
  overflow: hidden;
}
.x-sheet-form-plain-input .x-sheet-form-input-wrap {
  height: 100%;
  width: 100%;
  float: left;
}
.x-sheet-form-plain-input .x-sheet-form-input-inner {
  padding: 0 5px 0 5px;
  height: 100%;
}
.x-sheet-form-plain-input .x-sheet-form-input-source {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  color: #414141;
  display: inline-block;
}
.x-sheet-form-select {
  overflow: hidden;
  height: 100%;
}
.x-sheet-form-select-text {
  width: 90%;
  height: 100%;
  float: left;
  display: flex;
  align-items: center;
}
.x-sheet-form-select-icon {
  float: left;
  width: 9%;
  height: 100%;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAALCAYAAACzkJeoAAAAOklEQVQYV2P4//8/AwxHRkb+B2I4H13iP7ICbBJwBbgkwBhuLDaMXxLZddgk8duJTQKnP7E6CFkChAFpxL/ydoaj+QAAAABJRU5ErkJggg==);
  background-size: 30%;
  background-position: center;
  background-repeat: no-repeat;
}
.x-sheet-form-select-menu {
  background: #ffffff;
}
.x-sheet-form-select-menu-item {
  font-size: 13px;
  padding: 5px 10px 5px 10px;
}
.x-sheet-form-select-menu-item-title {
  line-height: 20px;
  margin-right: 20px;
  font-size: 15px;
}
